/** @format */

import axios from "axios";

export default {
  state: {
    berita: [],
    loading: false,
    errorMessage: null,
  },
  getters: {
    berita(state) {
      return state.berita;
    },
  },
  mutations: {
    SET_DATA: (state, berita) => (
      (state.berita = berita), (state.loading = true)
    ),

    ERROR: (state, errorMessage) => (state.errorMessage = errorMessage),
  },
  actions: {
    async getData({ commit }, id = "") {
      try {
        const res = await axios.get(`json/berita/${id}`);
        const berita = id ? res.data : res.data.data;
        commit("SET_DATA", berita);
      } catch (error) {
        const errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
        commit("ERROR", errorMessage);
      }
    },
  },
  namespaced: true,
};
