/** @format */

import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
// Profile
import Sejarah from "../views/profile/Sejarah.vue";
import VisiMisi from "../views/profile/VisiMisi.vue";
import Pimpinan from "../views/profile/Pimpinan.vue";
// Prodi
import Prodi from "../views/prodi/_id.vue";
// Galeri
import Galeri from "../views/galeri/";
// Tentang Kami
import TentangKami from "../views/tentang/";
// pengumuman
import Pengumuman from "../views/pengumuman/";
import PengumumanProdi from "../views/pengumuman/prodi.vue";
// berita
import DetailsBerita from "../views/berita/_id.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },

  {
    path: "/profile/Sejarah",
    name: "Sejarah",
    component: Sejarah,
  },

  {
    path: "/profile/VisiMisi",
    name: "VisiMisi",
    component: VisiMisi,
  },
  {
    path: "/profile/pimpinan",
    name: "Pimpinan",
    component: Pimpinan,
  },
  {
    path: "/prodi/:id",
    name: "Prodi",
    component: Prodi,
  },
  {
    path: "/galeri/",
    name: "Galeri",
    component: Galeri,
  },
  {
    path: "/tentangkami/",
    name: "TentangKami",
    component: TentangKami,
  },

  {
    path: "/pengumuman/",
    name: "Pengumuman",
    component: Pengumuman,
    children: [],
  },
  {
    path: "/pengumuman/prodi/:id",
    name: "PengumumanProdi",
    component: PengumumanProdi,
  },
  {
    path: "/berita/:id",
    name: "DetailsBerita",
    component: DetailsBerita,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,

  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});

export default router;
