/** @format */
import axios from "axios";

const state = {
  pengumuman: [],
  errorMessage: null,
  loading: false,
  currentPage: null,
  totalData: null,
};

const getters = {
  pengumuman(state) {
    return state.pengumuman;
  },
  getCurrentPages(state) {
    return state.currentPage;
  },
  getTotalData(state) {
    return state.totalData;
  },
};

const actions = {
  async getData({ commit }, page = 1) {
    try {
      const res = await axios.get(`json/pengumuman/`, { params: { page } });
      const pengumuman = res.data.data;
      const pages = res.data;
      commit("SET_DATA", pengumuman);
      commit("SET_PAGES", pages);
    } catch (error) {
      const errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
      commit("ERROR", errorMessage);
    }
  },
  async getDataProdi({ commit }, prodi_id) {
    try {
      const res = await axios.get(`json/pengumuman/prodi/${prodi_id}`);
      const pengumuman = res.data.data;
      commit("SET_DATA", pengumuman);
    } catch (error) {
      const errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
      commit("ERROR", errorMessage);
    }
  },
};

const mutations = {
  SET_DATA: (state, pengumuman) => (
    (state.pengumuman = pengumuman), (state.loading = true)
  ),

  SET_PAGES: (state, pages) => {
    (state.totalData = pages.total), (state.currentPage = pages.current_page);
  },

  ERROR: (state, errorMessage) => (state.errorMessage = errorMessage),
};

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters,
};
