<!-- @format -->

<template>
  <div class="col-12">
    <div class="row">
      <h4 class=" text-danger " v-if="errorMessage">{{ errorMessage }}</h4>
      <div
        class="col-sm-12 col-lg-8"
        v-if="pengumuman.length > 0"
        :class="{
          'animated bounceInLeft': pengumuman[0].prodi.kd_prodi === 'SI',
          'animated bounceInDown': pengumuman[0].prodi.kd_prodi === 'TG',
          'animated bounceInUp': pengumuman[0].prodi.kd_prodi === 'BI',
        }"
      >
        <div class="card bg-white pl-2 pt-2">
          <h3 class="card-title text-center">
            Pengumuman Program Studi {{ pengumuman[0].prodi.nm_prodi }}
          </h3>
        </div>
        <!-- {{-- Isi Pengumuman --}} -->
        <div class="row mt-3">
          <div
            class="col-sm-12 col-md-6"
            v-for="(item, index) in pengumuman"
            :key="index"
          >
            <a
              href="#"
              data-toggle="modal"
              data-target="#myModal"
              @click="
                openModal(item.id, item.judul_pengumuman, item.isi_pengumuman)
              "
            >
              <div class="card iq-mb-3">
                <div class="card-body">
                  <h4 class="card-title text-primary">
                    {{ item.judul_pengumuman }}
                  </h4>
                  <ul class="list-group list-group-flush">
                    <li class="list-group-item text-dark">
                      {{ item.prodi.nm_prodi }}
                    </li>
                    <li class="list-group-item text-dark">
                      {{ item.tgl_pengumuman | moment("dddd, DD MMMM YYYY") }}
                    </li>
                  </ul>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
      <div v-else class="col-sm-12 col-lg-8 iq-card pb-5 pt-5">
        <h4 class=" text-center">Belum ada pengumuman</h4>
      </div>
      <!-- Kanan -->
      <div class="col-sm-12 col-lg-4">
        <!-- Pengumuman -->
        <KumpulanVue />
      </div>

      <ModalVue
        v-if="id"
        :judul_pengumuman="judul_pengumuman"
        :isi_pengumuman="isi_pengumuman"
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import KumpulanVue from "../../components/pengumuman/Kumpulan.vue";
import ModalVue from "../../components/pengumuman/Modal.vue";

export default {
  data() {
    return {
      id: null,
      judul_pengumuman: null,
      isi_pengumuman: null,
      prodi_id: this.$route.params.id,
    };
  },
  components: {
    ModalVue,
    KumpulanVue,
  },
  methods: {
    ...mapActions("pengumuman", ["getDataProdi"]),
    openModal: function(id, judul, isi) {
      this.modal = true;
      this.id = id;
      (this.judul_pengumuman = judul), (this.isi_pengumuman = isi);
    },
  },

  computed: {
    ...mapState("pengumuman", ["loading", "errorMessage"]),
    ...mapGetters("pengumuman", ["pengumuman"]),
  },

  watch: {
    $route() {
      this.getDataProdi(this.$route.params.id);
    },
  },

  created() {
    this.getDataProdi(this.prodi_id);
  },
};
</script>

<style lang="scss" scoped></style>
