<!-- @format -->

<template>
  <div>
    <div class="iq-card">
      <!-- Keterangan Tunggu -->
      <h3 class="text-center" v-if="!loading">Mengambil Data ...</h3>
      <!-- Keterangan Error -->
      <h3 class="text-center text-danger">{{ errorMessage }}</h3>
      <div class="iq-card-body" v-if="loading">
        <div class="bd-example">
          <div
            id="carouselExampleCaptions"
            class="carousel slide"
            data-ride="carousel"
          >
            <ol class="carousel-indicators">
              <li
                data-target="#carouselExampleCaptions"
                v-for="(item, index) in images"
                :key="index"
                :data-slide-to="index"
                v-bind:class="{ active: index === 0 }"
              ></li>
            </ol>
            <div class="carousel-inner">
              <div
                class="carousel-item"
                v-for="(item, index) in images"
                :key="index"
                v-bind:class="{ active: index === 0 }"
              >
                <img
                  :src="baseUrl + '/storage/' + item.path_gambar"
                  class="d-block w-100"
                  alt="#"
                />
                <div class="carousel-caption d-none d-md-block">
                  <h4 class="text-white">
                    {{ item.kegiatan_det.kegiatan.nm_kegiatan }}
                  </h4>
                  <p>
                    {{ item.ket }}
                  </p>
                </div>
              </div>
            </div>
            <a
              class="carousel-control-prev"
              href="#carouselExampleCaptions"
              role="button"
              data-slide="prev"
            >
              <span
                class="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Previous</span>
            </a>
            <a
              class="carousel-control-next"
              href="#carouselExampleCaptions"
              role="button"
              data-slide="next"
            >
              <span
                class="carousel-control-next-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      images: [],
      loading: false,
      errorMessage: null,
      baseUrl: this.$myBaseUrl,
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios
        .get(`json/slide`)
        .then(
          (response) => (
            (this.images = response.data.data), (this.loading = true)
          )
        )
        .catch((error) => {
          (this.errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`),
            (this.loading = true);
        });
    },
  },
};
</script>

<style lang="scss" scoped></style>
