<template>
  <div>
    <h2 class="text-center">{{ berita.judul }}</h2>
    <span
      >Penulis: {{ berita.penulis }} -
      {{ berita.tgl_terbit | moment("dddd, DD MMMM YYYY") }}</span
    >
    <div class="col-12">
      <div class="row">
        <img
          :src="baseUrl + '/storage/' + berita.gambar_berita"
          width="200px"
          class="img-thumbnail m-auto"
          alt="Responsive image"
        />
      </div>
    </div>
    <p class="pt-2 text-dark" v-html="berita.isi_berita"></p>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      baseUrl: this.$myBaseUrl,
    };
  },
  metaInfo: {
    title: "Berita",
  },
  props: ["id"],
  methods: {
    ...mapActions("berita", ["getData"]),
  },

  computed: {
    ...mapState("berita", ["loading", "errorMessage"]),
    ...mapGetters("berita", ["berita"]),
  },

  created() {
    this.getData(this.id);
  },
};
</script>

