<!-- @format -->

<template>
  <div>
    <!-- Button trigger modal -->
    <VueSlickCarousel v-bind="settings" v-if="loading">
      <div class="col-12" v-for="item in pengumuman" :key="item.id">
        <a
          href="#"
          data-toggle="modal"
          data-target="#myModal"
          @click="
            openModal(item.id, item.judul_pengumuman, item.isi_pengumuman)
          "
        >
          <div class="col-12">
            <div
              class="iq-card iq-card-block iq-card-stretch iq-card-height iq-border-box iq-border-box-1 text-pengumuman"
              :class="{
                'text-success': item.prodi.kd_prodi === 'BI',
                'text-orange': item.prodi.kd_prodi === 'TG',
                'text-info': item.prodi.kd_prodi === 'SI',
              }"
            >
              <div class="iq-card-body">
                <h5 class="text-center mb-1 uppercase">
                  {{ item.prodi.nm_prodi }}
                </h5>
                <div class="d-flex align-items-center justify-content-between">
                  <h6>{{ item.judul_pengumuman }} {{ errorMessage }}</h6>
                </div>
              </div>
            </div>
          </div>
        </a>
      </div>
    </VueSlickCarousel>
    <modals
      v-if="id"
      :judul_pengumuman="judul_pengumuman"
      :isi_pengumuman="isi_pengumuman"
    />
  </div>
</template>

<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
import axios from "axios";
import modals from "./pengumuman/Modal.vue";
export default {
  components: {
    VueSlickCarousel,
    modals,
  },
  data() {
    return {
      settings: {
        dots: true,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 2000,
        pauseOnDotsHover: true,
        pauseOnFocus: true,
        pauseOnHover: true,
        focusOnSelect: true,
      },
      window: {
        width: 0,
        height: 0,
      },
      pengumuman: [],
      errorMessage: null,
      loading: false,
      id: null,
      judul_pengumuman: null,
      isi_pengumuman: null,
    };
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.getData();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      this.window.width = window.innerWidth;
      if (this.window.width <= 800) {
        this.settings.slidesToShow = 1;
      }
    },
    getData() {
      axios
        .get(`json/pengumuman`)
        .then(
          (response) => (
            (this.pengumuman = response.data.data), (this.loading = true)
          )
        )
        .catch((error) => {
          this.errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
        });
    },
    openModal: function(id, judul, isi) {
      this.modal = true;
      this.id = id;
      (this.judul_pengumuman = judul), (this.isi_pengumuman = isi);
    },
  },
};
</script>

<style lang="scss" scoped></style>
