<!-- @format -->

<template>
  <div class="col-sm-12 col-lg-8">
    <h4 class=" text-danger" v-if="errorMessage">{{ errorMessage }}</h4>
    <div class="card bg-white pl-2 pt-2">
      <h3 class="card-title text-center">
        Pengumuman Fakultas Sains & Teknologi
      </h3>
    </div>
    <!-- {{-- Isi Pengumuman --}} -->
    <div class="row mt-3">
      <div
        class="col-sm-12 col-md-6"
        v-for="(item, index) in pengumuman"
        :key="index"
      >
        <a
          href="#"
          data-toggle="modal"
          data-target="#myModal"
          @click="
            openModal(item.id, item.judul_pengumuman, item.isi_pengumuman)
          "
        >
          <div class="card iq-mb-3">
            <div class="card-body">
              <h4 class="card-title text-primary">
                {{ item.judul_pengumuman }}
              </h4>
              <ul class="list-group list-group-flush">
                <li class="list-group-item text-dark">
                  {{ item.prodi.nm_prodi }}
                </li>
                <li class="list-group-item text-dark">
                  {{ item.tgl_pengumuman | moment("dddd, DD MMMM YYYY") }}
                </li>
              </ul>
            </div>
          </div>
        </a>
      </div>
    </div>
    {{ currentPage }}
    <div class="col-12">
      <div class="row">
        <b-pagination
          class=" m-auto"
          v-if="getTotalData > 10"
          v-model="currentPage"
          :total-rows="getTotalData"
          :per-page="10"
          prev-text="Kembali"
          next-text="Selanjutnya"
          first-number
          last-number
          @change="gantiPage(currentPage)"
        ></b-pagination>
      </div>
    </div>
    <ModalVue
      v-if="id"
      :judul_pengumuman="judul_pengumuman"
      :isi_pengumuman="isi_pengumuman"
    />
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
import ModalVue from "./Modal.vue";

export default {
  data() {
    return {
      id: null,
      judul_pengumuman: null,
      isi_pengumuman: null,
      rows: 100,
      currentPage: 1,
    };
  },
  components: {
    ModalVue,
  },
  methods: {
    ...mapActions("pengumuman", ["getData"]),
    openModal: function(id, judul, isi) {
      this.modal = true;
      this.id = id;
      (this.judul_pengumuman = judul), (this.isi_pengumuman = isi);
    },

    gantiPage(page) {
      this.getData(page);
      window.scroll(0, 0);
    },
  },

  computed: {
    ...mapState("pengumuman", ["loading", "errorMessage"]),
    ...mapGetters("pengumuman", [
      "pengumuman",
      "getCurrentPages",
      "getTotalData",
    ]),
  },

  created() {
    this.getData(this.currentPage);
  },
};
</script>

<style lang="scss" scoped></style>
