<!-- @format -->

<template>
  <div class="col-sm-12 col-lg-8">
    <div class="card bg-white pl-2 pt-2">
      <h3 class="card-title text-center">Galeri Fakultas Sains & Teknologi</h3>
    </div>
    <!-- {{-- Isi Galeri --}} -->
    <div class="row mt-3">
      <div class="col-sm-12">
        <div class="iq-card">
          <div class="iq-card-body text-center">
            <!-- Keterangan Tunggu -->
            <h4 class="text-center" v-if="!loading">Mengambil Data ...</h4>
            <silent-box :gallery="gallery"></silent-box>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  components: {},
  data() {
    return {
      gallery: [],
      images: [],
      loading: false,
      baseUrl: this.$myBaseUrl,
    };
  },
  methods: {
    getData() {
      axios
        .get(`json/galeri`)
        .then(
          (response) => (
            (this.images = response.data.data),
            (this.loading = true),
            this.images.forEach((element) => {
              this.gallery.push({
                src: this.baseUrl + "/storage/" + element.path_gambar,
                description: element.kegiatan_det.kegiatan.nm_kegiatan,
                thumbnailHeight: "220px",
                thumbnailWidth: "220px",
              });
            })
          )
        )
        .catch((error) => {
          this.errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
        });
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped></style>
