/** @format */
import axios from "axios";

export default {
  state: {
    dosen: [],
    loading: false,
    errorMessage: null,
  },
  getters: {
    getDosen: (state) => {
      return state.dosen;
    },
  },
  mutations: {
    setDosen(state, dosen) {
      state.dosen = dosen;
    },
  },
  actions: {
    async getDataDosenProdi({ commit }, id) {
      try {
        const res = await axios.get(`json/dosen/${id}`);
        const dosen = res.data.data;
        commit("setDosen", dosen);
      } catch (error) {
        const errorMessage = `Terjadi Kesalahan dalam pengambilan data ${error.message}`;
        commit("ERROR", errorMessage);
      }
    },
  },
  namespaced: true,
};
